
/**
 * 
 *
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */
export function throttle(func, wait = 500, immediate = false) {
	let timer; let
		flag
	if (immediate) {
		if (!flag) {
			flag = true
			// wait
			typeof func === 'function' && func()
			timer = setTimeout(() => {
				flag = false
			}, wait)
		}
	} else if (!flag) {
		flag = true
		// wait
		timer = setTimeout(() => {
			flag = false
			typeof func === 'function' && func()
		}, wait)
	}
}



/**
 * wait
 *
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */

export function debounce(func, wait = 500, immediate = false) {
	let timeout = null
	// 
	if (timeout !== null) clearTimeout(timeout)
	// 
	if (immediate) {
		const callNow = !timeout
		timeout = setTimeout(() => {
			timeout = null
		}, wait)
		if (callNow) typeof func === 'function' && func()
	} else {
		// timeoutwaitfunc
		timeout = setTimeout(() => {
			typeof func === 'function' && func()
		}, wait)
	}
}


/**
 * 
 *
 * @param {Number} horizontal 
 * @param {Number} vertical  
 * @param {Number} distance  
 * @return {x , y , z}  
 */

export function lightPosition(horizontal, vertical, distance) {
	const x = distance * Math.sin(horizontal) * Math.cos(vertical);
	const y = distance * Math.sin(vertical);
	const z = distance * Math.cos(horizontal) * Math.cos(vertical);
	return {x,y,z}
}