<template>
  <div class="edit-box">
    <div class="header">
      <span></span>
      <el-switch v-model="config.visible" @change="onChangeAnimationSwitch" />
    </div>
    <!--  -->
    <div class="options" :class="optionDisabled">
      <el-scrollbar max-height="250px" v-if="state.modelAnimation.length">
        <div
          class="option"
          :class="config.animationName == animation.name ? 'option-active' : ''"
          @click="onChangeAnimationType(animation)"
          v-for="animation in state.modelAnimation"
          :key="animation.name"
        >
          <el-space>
            <div class="icon-name">
              {{ animation.name }}
            </div>
            <div class="check" v-show="config.animationName == animation.name">
              <el-icon size="20px" color="#2a3ff6"><Check /></el-icon>
            </div>
          </el-space>
        </div>
      </el-scrollbar>
      <el-empty v-else description="" :image-size="200" />
    </div>
    <!--  -->
    <div class="header">
      <span></span>
    </div>
    <div class="options" :class="optionDisabled">
      <div class="option">
        <el-space>
          <el-icon><Switch /></el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-radio-group v-model="config.loop" @change="onUplateAnimation">
          <el-radio-button label="LoopOnce"></el-radio-button>
          <el-radio-button label="LoopRepeat"></el-radio-button>
          <el-radio-button label="LoopPingPong"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="option">
        <el-space>
          <el-icon><VideoPlay /></el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-slider
          show-input
          v-model="config.timeScale"
          @change="onUplateAnimation"
          :step="0.01"
          :min="0"
          :max="1"
        />
      </div>
      <div class="option">
        <el-space>
          <el-icon><Sort /></el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-slider
          show-input
          v-model="config.weight"
          @change="onUplateAnimation"
          :step="0.01"
          :min="0"
          :max="1"
        />
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, reactive, computed, onMounted, getCurrentInstance } from "vue";
import { useStore } from "vuex";
const store = useStore();
const { $bus } = getCurrentInstance().proxy;
const config = reactive({
  visible: true,
  animationName: null, //
  loop: "LoopRepeat", //  TODO:LoopOnce  LoopRepeat   LoopPingPong 
  timeScale: 1, // 
  weight: 1, // 
  animations: [],
});
const optionDisabled = computed(() => {
  return config.visible ? "" : "disabled";
});

const state = reactive({
  modelApi: computed(() => {
    return store.state.modelApi;
  }),
  // 
  modelAnimation: computed(() => {
    if (store.state.modelApi) {
      return store.state.modelApi.modelAnimation;
    }
    return [];
  }),
});

onMounted(() => {
  if (state.modelAnimation.length) {
    config.animationName = state.modelAnimation[0].name;
  }
  // 
  $bus.on("model-update", () => {
    if (state.modelAnimation.length) {
      const animationName = state.modelAnimation[0].name;
      // 
      Object.assign(config, {
        visible: true,
        animationName, //
        loop: "LoopRepeat", //  TODO:LoopOnce  LoopRepeat   LoopPingPong 
        timeScale: 1, // 
        weight: 1, // 
      });
    }
  });
});

// /
const onChangeAnimationSwitch = () => {
  const { visible } = config;
  if (visible) {
    onUplateAnimation();
  } else {
    state.modelApi.onClearAnimation();
  }
};
// 
const onChangeAnimationType = ({ name }) => {
  config.animationName = name;
  config.animations = state.modelAnimation;
  state.modelApi.onStartModelAnimaion(config);
};
// 
const onUplateAnimation = () => {
  config.animations = state.modelAnimation;
  state.modelApi.onStartModelAnimaion(config);
};
</script>
<style scoped lang="scss"></style>
